import { Injectable } from "@angular/core";
import { AppConstant } from './app.constant';
export class UserMask {
    hiddenItems: Array<string>;
}
export class User {
    name: string;
    username: string;
    userAccessLevel: string;
    userId: string;
    /**
     *
     */
    constructor() {
        this.name = "";
        this.userAccessLevel = "";
        this.userId = "";
    }
}

const memberMask: UserMask = {
    hiddenItems: [
        
        'link_hidden'
    ],
}
const adminMask: UserMask = {

    hiddenItems: [

    ],
}
const agentMask: UserMask = {

    hiddenItems: [
       
        'link_upcoming_match',
    ],
}


@Injectable()
export class ApplicationHelper {
    private user: User
    constructor() {
    }
    public setloggedInUserInfo(user: User) {
        this.user = user;
    }
    public getloggedInUserInfo() {
        return this.user;
    }

    public getUserAccessLevel() {
        return JSON.parse(localStorage.getItem(AppConstant.USER_TYPE));
    }
    public getLoggedInUserId() {
        return this.user.userId;
    }

    public getUserRoleMask() {
        switch (this.getUserAccessLevel()) {
            case 0:
                return adminMask;
            case 1:
                return agentMask;
            case 2:
                return memberMask;
            default:
                return null;
        }
    }
}